<script setup>
import { gameData, resetSelection, unequipItem } from '../stores/gameData.js';
import ItemCard from './ItemCard.vue';

const selectSlot = (index) => {
  if (gameData.activeSlotIndex === index) {
    resetSelection();
  } else {
    gameData.activeSlotIndex = index;
  }
};

const handleUnequip = (index) => {
  unequipItem(index);
};
</script>

<template>
  <div class="bottom-bar">
    <div class="slots-container">
      
      <div 
        class="slot-wrapper" 
        v-for="(slotData, index) in gameData.slots" 
        :key="index"
        @click="selectSlot(index)"
      >
        <ItemCard
          :item="slotData.item"
          :placeholder-type="slotData.type"
          :is-selected="gameData.activeSlotIndex === index"
          :show-name="false"
        >
          <template #overlay>
            <button 
              v-if="slotData.item"
              class="unequip-btn"
              @click.stop="handleUnequip(index)"
            >
              √ó
            </button>
          </template>
        </ItemCard>
      </div>

    </div>
  </div>
</template>

