<script setup>
import { computed } from 'vue';
import StatDisplay from './StatDisplay.vue';

const props = defineProps({
  item: {
    type: Object,
    default: null
  },
  // ÏïÑÏù¥ÌÖúÏù¥ ÏóÜÏùÑ Îïå ÌëúÏãúÌï† ÌÉÄÏûÖ (placeholderÏö©)
  placeholderType: {
    type: String,
    default: 'sentinel'
  },
  // ÏÑ†ÌÉùÎêú ÏÉÅÌÉú (highlight)
  isSelected: {
    type: Boolean,
    default: false
  },
  // ÎπÑÌôúÏÑ± ÏÉÅÌÉú (dimmed)
  isDisabled: {
    type: Boolean,
    default: false
  },
  // Ïù¥Î¶Ñ ÌÖçÏä§Ìä∏ ÌëúÏãú Ïó¨Î∂Ä (BottomPanelÏóêÏÑúÎäî Í≥µÍ∞Ñ Ï†àÏïΩÏùÑ ÏúÑÌï¥ false)
  showName: {
    type: Boolean,
    default: true
  }
});

// ÏïÑÏù¥ÌÖúÏù¥ ÏûàÏúºÎ©¥ ÏïÑÏù¥ÌÖúÏùò ÌÉÄÏûÖÏùÑ, ÏóÜÏúºÎ©¥ placeholderTypeÏùÑ ÏÇ¨Ïö©
const displayType = computed(() => {
  if (props.item && props.item.stats && props.item.stats.length > 0) {
    return props.item.stats[0].type;
  }
  return props.placeholderType;
});

// ÏïÑÏù¥ÌÖúÏùò Î©îÏù∏ Î†àÎ≤® (Îì±Í∏â Î∞î ÏÉâÏÉÅÏö©)
const mainLevel = computed(() => {
  if (props.item && props.item.stats && props.item.stats.length > 0) {
    return props.item.stats[0].level;
  }
  return 1;
});
</script>

<template>
  <div 
