import { reactive } from 'vue';
import * as XLSX from 'xlsx';

const CURRENT_LANG = "kr";

export const gameData = reactive({
  stats: [],      
  statMap: {},    
  statCodeMap: {},
  lczMap: {},
  isLoaded: false,

  // Ïä¨Î°Ø 6Í∞ú
  slots: [
    { type: 'sentinel', item: null },
    { type: 'sentinel', item: null },
    { type: 'sentinel', item: null },
    { type: 'sentinel', item: null },
    { type: 'vanguard', item: null }, 
    { type: 'bulwark', item: null }  
  ],
  activeSlotIndex: -1, 
});

export const resetSelection = () => {
  gameData.activeSlotIndex = -1;
};

// Ïû•Ï∞© Ìï®Ïàò
export const equipItem = (item) => {
  if (gameData.activeSlotIndex >= 0 && gameData.activeSlotIndex < gameData.slots.length) {
    gameData.slots[gameData.activeSlotIndex].item = item;
    console.log(`Slot ${gameData.activeSlotIndex} equipped.`);
    resetSelection();
  }
};

// [ÏàòÏ†ï] ÌÉàÏ∞© Ìï®Ïàò (selection Ï¥àÍ∏∞Ìôî Ï∂îÍ∞Ä)
export const unequipItem = (slotIndex) => {
  if (slotIndex >= 0 && slotIndex < gameData.slots.length) {
    gameData.slots[slotIndex].item = null;
    console.log(`Slot ${slotIndex} unequipped.`);
    
    // ÌÉàÏ∞© ÌõÑÏóêÎèÑ ÏÑ†ÌÉù ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî
    resetSelection();
  }
};

/* --- Í∏∞Ï°¥ Î°úÎî© Î°úÏßÅ Ïú†ÏßÄ --- */
export const loadSharedData = async () => {
    if (gameData.isLoaded) return; 

    try {
        const response = await fetch('/assets/rpfv-data.xlsx');
        const arrayBuffer = await response.arrayBuffer();
        const workbook = XLSX.read(arrayBuffer, { type: 'array' });
        const sheet = workbook.Sheets['Stats'];
        const rawData = XLSX.utils.sheet_to_json(sheet);
    
        const processedStats = rawData.map(row => {
          let parsedValues = [];
          if (row.values) {
            const valStr = row.values.toString();
            parsedValues = valStr.split('/').map(v => v.trim());
          }
          
          return {
            key: row.key,
            code1: row.code1,
            code2: row.code2,
            code3: row.code3,
            isMajor: row.isMajor =="true"?true : false,
            factorType: row.factorType,
            maxLevel: row.maxLevel || 6,
            values: parsedValues,
            nameKey: `${row.key}@name`,
            nicknameKey: `${row.key}@nickname`,
            descKey: `${row.key}@desc`,
          };
        });
    
        gameData.stats = processedStats;
        const statKeyMap = {};
        processedStats.forEach(stat => {
          if (stat.key) statKeyMap[stat.key] = stat;
        });
        gameData.statMap = statKeyMap;
        
        const statCodeMap = {};
        processedStats.forEach(stat => {
          if(stat.code1) statCodeMap[stat.code1] = {statKey:stat.key, level:1};
          if(stat.code2) statCodeMap[stat.code2] = {statKey:stat.key, level:2};
          if(stat.code3) statCodeMap[stat.code3] = {statKey:stat.key, level:3};
        });
        gameData.statCodeMap = statCodeMap;
    
        const lczSheet = workbook.Sheets['Localization'];
        const lczJsonData = XLSX.utils.sheet_to_json(lczSheet);
        gameData.lczMap = lczJson2Map(lczJsonData);
    
        gameData.isLoaded = true;
        console.log(`[SharedData] Loaded ${processedStats.length} stats.`);
    
      } catch (error) {
        console.error("[SharedData] Î°úÎî© Ïã§Ìå®:", error);
      }
};

const lczJson2Map = (jsonData) => {
    const result = { kr: {}, en: {}, cn: {} };
    jsonData.forEach((row) => {
      const key = row.name; 
      if (key) {
        if (row.kr !== undefined) result.kr[key] = row.kr;
        if (row.en !== undefined) result.en[key] = row.en;
        if (row.cn !== undefined) result.cn[key] = row.cn;
      }
    });
    return result
};

export const lcz = (key)=>{
    const lczDict = gameData.lczMap[CURRENT_LANG];
    return lczDict[key] || key;
};