<script setup>
import { ref, onMounted, computed, watch, nextTick } from 'vue';
import * as XLSX from 'xlsx';
import { gameData, loadSharedData, lcz, equipItem } from '../stores/gameData.js';
import ItemCard from './ItemCard.vue';

const items = ref([]);
const activeFilter = ref('all');
const selectedItemId = ref(null);

const gridContainer = ref(null);

// Ï†ïÎ†¨ Í¥ÄÎ†® ÏÉÅÌÉú
const sortCriterion = ref('rarity'); 
const sortDirection = ref('desc');   
const isDropdownOpen = ref(false);   

const filterOptions = [
  { label: 'ALL', value: 'all' },
  { label: 'SENTINEL', value: 'sentinel' },
  { label: 'VANGUARD', value: 'vanguard' },
  { label: 'BULWARK', value: 'bulwark' },
  { label: 'SUPPORT', value: 'support' }
];

const sortOptions = [
  { label: 'Î†àÏñ¥ÎèÑ', value: 'rarity' },
  { label: 'ÌöçÎìù ÏàúÏÑú', value: 'acquisition' }
];

const selectSortCriterion = (value) => {
  sortCriterion.value = value;
  isDropdownOpen.value = false;
};

const toggleSortDirection = () => {
  sortDirection.value = sortDirection.value === 'asc' ? 'desc' : 'asc';
};

const currentSortLabel = computed(() => {
  const option = sortOptions.find(o => o.value === sortCriterion.value);
  return option ? option.label : 'Ï†ïÎ†¨';
});

watch(
  () => gameData.activeSlotIndex,
  async (newIndex) => {
    if (newIndex !== -1) {
      const targetSlot = gameData.slots[newIndex];
      activeFilter.value = targetSlot.type;
      
      if (targetSlot.item) {
        selectedItemId.value = targetSlot.item.id;
      } else {
        selectedItemId.value = null;
      }

      await nextTick();
      if (gridContainer.value) {
        gridContainer.value.scrollTop = 0;
      }

    } else {
      activeFilter.value = 'all'; 
      selectedItemId.value = null;
    }
  }
);

const selectItem = (id) => { 
  if (gameData.activeSlotIndex === -1) return;
  selectedItemId.value = id; 
};

const handleEquip = (item) => {
  equipItem(item);
};

/* --- Ïú†Ìã∏Î¶¨Ìã∞ --- */

const buildItemName = (statList) => {
  if(statList.length >= 2)
    return statList[1].nickname + "¬∑" + statList[0].nickname + rankToName(statList[0].level);
  else if (statList.length === 1)
    return statList[0].nickname;
  return "unknown";
};

const buildItemIconId = (statList) => {
  if(statList.length == 0) return 'unknown';
  const mainStat = statList[0];
  return `${mainStat.key}_${mainStat.level}`;
};

const rankToName = (level) => {
  if(level == 3) return 'Œ≥';
  if(level == 2) return 'Œ≤';
  if(level == 1) return 'Œ±';
  return '';
}

const loadUserData = async () => {
  try {
    await loadSharedData();
    const userResponse = await fetch('/assets/userdata.csv');
    if (!userResponse.ok) throw new Error(`HTTP error! status: ${userResponse.status}`);
    const userBuffer = await userResponse.arrayBuffer();
    const userWorkbook = XLSX.read(userBuffer, { type: 'array' });
    const userSheet = userWorkbook.Sheets[userWorkbook.SheetNames[0]];
    const userRaw = XLSX.utils.sheet_to_json(userSheet);

    items.value = userRaw.map((row, index) => {
      // 1. Ïä§ÌÉØ ÌååÏã± Î°úÏßÅ (Í∏∞Ï°¥ Ïú†ÏßÄ)
      const statsList = [];
      const statRowNames = ['stat1', 'stat2', 'stat3'];

      statRowNames.forEach(codeRowKey => {        
        const statCode = row[codeRowKey];
        if (statCode) {
          const matchItem = gameData.statCodeMap[statCode];
          const statKey = matchItem.statKey;
          const statLevel = matchItem.level;
          const statInfo = gameData.statMap[statKey];

          const finalType = statInfo ? statInfo.factorType : 'unknown';
          const finalMaxLevel = statInfo ? statInfo.maxLevel : 6;
          const finalName = lcz(statInfo.nameKey);
          const finalNickname = lcz(statInfo.nicknameKey);
          
          statsList.push({
            key: statKey,
            level: statLevel,
            name: finalName,
            type: finalType,
            maxLevel: finalMaxLevel,
            nickname: finalNickname
          });
        }
      });

      // [ÏàòÏ†ï] 2. Î°úÎî© ÏãúÏ†êÏóê BigInt Î≥ÄÌôò ÏàòÌñâ
      let idBigInt = 0n;
      try {
        // "U184..." -> 'U' Ï†úÍ±∞ ÌõÑ BigInt Î≥ÄÌôò
        if (row.uid) {
          idBigInt = BigInt(String(row.uid).replace('U', ''));
        }
      } catch (e) {
        // Î≥ÄÌôò Ïã§Ìå® Ïãú 0n (ÏïàÏ†ÑÏû•Ïπò)
        idBigInt = 0n;
      }

      const rank = statsList[0].level || 0
      const type = statsList[0].type || 0
      return {
        id: idBigInt, 
        index: index,
        name: buildItemName(statsList),
        icon: buildItemIconId(statsList),
        stats: statsList,
        rank: rank,
        type : type
      };
    });
  } catch (error) { console.error("Îç∞Ïù¥ÌÑ∞ Î°úÎî© Ïã§Ìå®:", error); }
};

// [ÏàòÏ†ï] Ï†ïÎ†¨ Î°úÏßÅ Í∞ÑÏÜåÌôî
const filteredItems = computed(() => {
  let result = [...items.value];
  
  if (activeFilter.value !== 'all') {
    result = result.filter(item => item.type === activeFilter.value);
  }

  result.sort((a, b) => {
    let diff = 0;

    if (sortCriterion.value === 'rarity') {
      // Î†àÏñ¥ÎèÑ Í∏∞Ï§Ä: level ÎπÑÍµê
      const levelA = a.rank || 0;
      const levelB = b.rank || 0;
      diff = levelA - levelB;
      
      // Î†àÏñ¥ÎèÑÍ∞Ä Í∞ôÏúºÎ©¥ ÌöçÎìù ÏàúÏÑú(BigInt) ÎπÑÍµê
      if (diff === 0) {
        if (a.id < b.id) diff = -1;
        else if (a.id > b.id) diff = 1;
      }
    } else {
      // ÌöçÎìù ÏàúÏÑú Í∏∞Ï§Ä: BigInt ÎπÑÍµê
      if (a.id < b.id) diff = -1;
      else if (a.id > b.id) diff = 1;
    }

    // Î∞©Ìñ• Ï†ÅÏö©
    return sortDirection.value === 'asc' ? diff : -diff;
  });

  if (gameData.activeSlotIndex !== -1) {
    const currentSlot = gameData.slots[gameData.activeSlotIndex];
    if (currentSlot && currentSlot.item) {
        const equippedId = currentSlot.item.id;
        
        const equippedIndex = result.findIndex(i => i.id === equippedId);
        if (equippedIndex > -1) {
          const [equippedItem] = result.splice(equippedIndex, 1);
          result.unshift(equippedItem);
        }
    }
  }
  
  return result;
});

onMounted(() => { loadUserData(); });
</script>

<template>
  <div class="center-panel">
    
    <div class="panel-header">
      <div class="header-top">
        <h2>INVENTORY</h2>
        <span class="count">{{ filteredItems.length }} ITEMS</span>
      </div>

      <div class="filter-bar">
        <button 
          v-for="filter in filterOptions" 
          :key="filter.value"
          class="filter-btn"
          :class="[ filter.value, { 'active': activeFilter === filter.value } ]"
          :disabled="gameData.activeSlotIndex !== -1"
          @click="activeFilter = filter.value"
        >
          {{ filter.label }}
        </button>
      </div>

      <div class="sort-bar">
        <div class="sort-dropdown-container">
          <div 
            class="sort-trigger" 
            @click="isDropdownOpen = !isDropdownOpen"
          >
            <div class="icon-diamond">‚ùñ</div> <span>{{ currentSortLabel }}</span>
            <span class="arrow-down">‚ñº</span>
          </div>

          <div v-if="isDropdownOpen" class="sort-menu">
            <div 
              v-for="opt in sortOptions" 
              :key="opt.value"
              class="sort-option"
              :class="{ 'selected': sortCriterion === opt.value }"
              @click="selectSortCriterion(opt.value)"
            >
              {{ opt.label }}
            </div>
          </div>
          
          <div v-if="isDropdownOpen" class="dropdown-overlay" @click="isDropdownOpen = false"></div>
        </div>

        <button class="sort-direction-btn" @click="toggleSortDirection">
          <span v-if="sortDirection === 'desc'">‚¨á</span> 
          <span v-else>‚¨Ü</span> 
        </button>
      </div>

    </div>

    <div class="grid-container custom-scroll" ref="gridContainer">
      
      <ItemCard
        v-for="item in filteredItems" 
        :key="item.id"
        class="inventory-card"
        :item="item"
        :is-selected="selectedItemId === item.id"
        :is-disabled="gameData.activeSlotIndex === -1"
        :show-name="true"
        @click="selectItem(item.id)"
      >
        <template #overlay>
          <button 
            v-if="gameData.activeSlotIndex >= 0 && selectedItemId === item.id"
            class="equip-btn"
            @click.stop="handleEquip(item)"
          >
              Ïû•Ï∞©
          </button>
        </template>
      </ItemCard>

    </div>
  </div>
</template>

<style scoped>
/* Ïä§ÌÉÄÏùºÏùÄ Î≥ÄÍ≤ΩÏÇ¨Ìï≠ ÏóÜÏùå */
.center-panel {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%; 
  background: rgba(0, 0, 0, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden; 
}

.panel-header { 
  flex-shrink: 0;
  padding: 15px 20px 0 20px; 
  border-bottom: 1px solid rgba(255, 255, 255, 0.1); 
  display: flex; 
  flex-direction: column; 
  gap: 10px; 
  background: rgba(0, 0, 0, 0.2); 
  padding-bottom: 10px;
}

.header-top { display: flex; justify-content: space-between; align-items: center; }
.panel-header h2 { margin: 0; font-size: 1.2rem; letter-spacing: 2px; color: #fff; text-shadow: 0 0 5px rgba(255, 255, 255, 0.5); }
.count { font-size: 0.8rem; color: #00d0ff; font-weight: bold; }
.filter-bar { display: flex; gap: 8px; }

.filter-btn { background: rgba(255, 255, 255, 0.05); border: 1px solid rgba(255, 255, 255, 0.1); color: #888; padding: 6px 12px; font-size: 0.75rem; cursor: pointer; transition: all 0.2s; text-transform: uppercase; font-weight: bold; letter-spacing: 1px; }
.filter-btn:disabled { opacity: 0.5; cursor: not-allowed; }
.filter-btn:hover:not(:disabled) { background: rgba(255, 255, 255, 0.1); color: #ccc; }
.filter-btn.active { color: #fff; border-color: currentColor; background: rgba(255, 255, 255, 0.15); box-shadow: 0 0 8px rgba(0,0,0,0.5); }
.filter-btn.all.active { color: #fff; border-color: #fff; }
.filter-btn.sentinel.active { color: #a060ff; border-color: #a060ff; box-shadow: 0 0 5px #a060ff; }
.filter-btn.vanguard.active { color: #ff4040; border-color: #ff4040; box-shadow: 0 0 5px #ff4040; }
.filter-btn.bulwark.active { color: #00d0ff; border-color: #00d0ff; box-shadow: 0 0 5px #00d0ff; }
.filter-btn.support.active { color: #40ff80; border-color: #40ff80; box-shadow: 0 0 5px #40ff80; }

.sort-bar {
  display: flex;
  gap: 8px;
  align-items: center;
  height: 32px; 
  margin-top: 5px;
}

.sort-dropdown-container {
  position: relative;
  width: 180px; 
  height: 100%;
}

.sort-trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  background: rgba(30, 30, 30, 0.8);
  border: 1px solid #555;
  border-radius: 4px;
  color: #ddd;
  font-size: 0.8rem;
  cursor: pointer;
  box-sizing: border-box;
  transition: border-color 0.2s;
}

.sort-trigger:hover {
  border-color: #888;
}

.icon-diamond {
  color: #888;
  margin-right: 8px;
  font-size: 1.2em;
}

.arrow-down {
  font-size: 0.7em;
  color: #888;
}

.sort-menu {
  position: absolute;
  top: 105%;
  left: 0;
  width: 100%;
  background: #2a2a2a;
  border: 1px solid #555;
  border-radius: 4px;
  z-index: 100;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0,0,0,0.5);
}

.sort-option {
  padding: 10px 12px;
  font-size: 0.8rem;
  color: #ccc;
  cursor: pointer;
  transition: background 0.2s;
}

.sort-option:hover {
  background: #3a3a3a;
  color: #fff;
}

.sort-option.selected {
  background: #444;
  color: #fff;
  font-weight: bold;
}

.dropdown-overlay {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 99;
}

.sort-direction-btn {
  height: 100%;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(30, 30, 30, 0.8);
  border: 1px solid #555;
  border-radius: 4px;
  color: #ddd;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}

.sort-direction-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.grid-container { 
  flex: 1; 
  overflow-y: auto; 
  padding: 20px; 
  display: grid; 
  grid-template-columns: repeat(auto-fill, minmax(130px, 1fr)); 
  gap: 15px; 
  scrollbar-width: thin;
  scrollbar-color: #444 transparent;
}

.inventory-card {
  min-height: 160px;
}

.custom-scroll::-webkit-scrollbar { width: 6px; }
.custom-scroll::-webkit-scrollbar-track { background: transparent; }
.custom-scroll::-webkit-scrollbar-thumb { background: #444; border-radius: 3px; }
.custom-scroll::-webkit-scrollbar-thumb:hover { background: #666; }

.equip-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 10;
  background-color: #00d0ff;
  color: #000;
  border: none;
  border-radius: 2px;
  padding: 4px 8px;
  font-size: 0.75rem;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0,0,0,0.5);
  animation: popIn 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.equip-btn:hover { background-color: #fff; box-shadow: 0 0 10px #fff; }
@keyframes popIn { from { transform: scale(0); opacity: 0; } to { transform: scale(1); opacity: 1; } }
</style>