<script setup>
import { computed, onMounted } from 'vue';
import { gameData, loadSharedData, lcz } from '../stores/gameData.js';

// Ï†ïÎ†¨ ÏàúÏÑú Ï†ïÏùò (sentinel -> vanguard -> bulwark -> support)
const TYPE_ORDER = {
  'sentinel': 1,
  'vanguard': 2,
  'bulwark': 3,
  'support': 4
};

const aggregatedStats = computed(() => {
  const aggMap = {};

  // 1. Ïä¨Î°Ø Ï†ÑÏ≤¥ ÏàúÌöå
  gameData.slots.forEach(slot => {
    if (!slot.item || !slot.item.stats) return;

    slot.item.stats.forEach(itemStat => {
      // 2. Ïä§ÌÉØ Îß§Ïπ≠
      const masterStat = gameData.stats.find(s => lcz(s.nameKey) === itemStat.name);
      
      if (masterStat) {
        if (!aggMap[masterStat.key]) {
          aggMap[masterStat.key] = {
            ...masterStat,
            rawLevel: 0,
            displayLevel: 0,
            isOverflow: false,
            overVal: 0
          };
        }
        aggMap[masterStat.key].rawLevel += itemStat.level;
      }
    });
  });

  // 3. MaxLevel Ï†úÌïú, Ï¥àÍ≥ºÎ∂Ñ Í≥ÑÏÇ∞ Î∞è Ï†ïÎ†¨ Ï†ÅÏö©
  return Object.values(aggMap).map(stat => {
    if (stat.rawLevel > stat.maxLevel) {
      stat.displayLevel = stat.maxLevel;
      stat.isOverflow = true;
      stat.overVal = stat.rawLevel - stat.maxLevel;
    } else {
      stat.displayLevel = stat.rawLevel;
      stat.isOverflow = false;
      stat.overVal = 0;
    }
    return stat;
  }).sort((a, b) => {
    // 1. ÌÉÄÏûÖ Ï†ïÎ†¨
    const rankA = TYPE_ORDER[a.factorType] || 99;
    const rankB = TYPE_ORDER[b.factorType] || 99;
    
    if (rankA != rankB) {
      return rankA - rankB;
    }

    // 2. isMajor Ï†ïÎ†¨ (trueÍ∞Ä Î®ºÏ†Ä Ïò§ÎèÑÎ°ù)
    const majorA = a.isMajor ? 1 : 0;
    const majorB = b.isMajor ? 1 : 0;
    
    if (majorA != majorB) {
      return majorA ? -1 : 1; 
    }

    // 3. Key Í∞í Ï†ïÎ†¨
    if (a.key < b.key) return -1;
    if (a.key > b.key) return 1;
    return 0;
  });
});

const getRichDesc = (stat) => {
  if (!stat.descKey) return "";
  
  const currentLevel = stat.displayLevel;
  const currentVal = currentLevel > 0 ? (stat.values[currentLevel - 1] || '?') : '-';
  
  const coloredVal = `<span class="val-highlight">${currentVal}</span>`;
  const descText = lcz(stat.descKey);
  
  return descText.replace('{0}', coloredVal).replace(/\n/g, '<br>');
};

onMounted(() => {
  loadSharedData();
});
</script>

<template>
  <div class="panel-container">
    <div class="header">Active Traits</div>
    
    <div class="list-area">
      <div v-if="aggregatedStats.length === 0" class="empty-msg">
        No active traits. <br>Equip items to see stats.
      </div>

      <div class="list-item" v-for="(stat, index) in aggregatedStats" :key="index">
        
        <div class="lvl-badge" :class="{ 'max-lvl': stat.displayLevel === stat.maxLevel }">
          Lv.{{ stat.displayLevel }}
        </div>

        <div class="item-middle">
          <div class="stat-name-row">
            <span 
              class="stat-name" 
              :class="{ 'is-major': stat.isMajor }"
            >
              {{ lcz(stat.nameKey) }}
            </span>
            
            <span v-if="stat.isOverflow" class="overflow-warning">
              ‚ö†Ô∏è +{{ stat.overVal }}
            </span>
          </div>
          
          <div class="stat-desc" v-html="getRichDesc(stat)"></div>
        </div>

        <div class="icon-track">
          <div 
            v-for="n in stat.maxLevel" 
            :key="n"
            class="level-block"
            :class="[
              stat.factorType, 
              { 'is-active': n <= stat.displayLevel }
            ]"
          ></div>
        </div>

      </div>
    </div>
  </div>
</template>

